.prizes {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  padding: 14.6rem 0 7.7rem;
  width: 100%;

  @include scale-in-block;

  @media (min-width: $stop-scaling) {
    padding: 146px 0 65px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    padding: 5.9rem 0;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    padding: 9rem 0 6rem;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    padding: 5rem 0 1.5rem;
  }
}

.prizes__title {
  margin: 0 0 6rem;
  text-transform: uppercase;
  font-family: $font-alt;
  font-weight: 400;
  font-size: 4.8rem;
  line-height: 0.906;
  letter-spacing: -0.04em;

  @media (min-width: $stop-scaling) {
    font-size: 48px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    margin-bottom: 1.8rem;
    font-size: 3.6rem;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    font-size: 3rem;
    margin-bottom: 2rem;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    font-size: 2rem;
    margin-bottom: 1rem;
  }
}

.prizes__lead {
  margin-left: 5.8rem;
  margin-bottom: -5.6rem;
  max-width: 34rem;
  text-transform: uppercase;
  font-size: 2.4rem;
  line-height: 1.2;
  opacity: 0;

  .screen--prizes.active & {
    animation: text-slide-up 0.3s ease-out 0.85s both;

    @media (prefers-reduced-motion: reduce) {
      animation: none;
    }
  }

  @media (min-width: $stop-scaling) {
    margin-left: 58px;
    margin-bottom: -56px;
    max-width: 340px;
    font-size: 24px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    margin-left: 2.5rem;
    margin-bottom: 3.2rem;
    max-width: 22rem;
    font-size: 1.6rem;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    margin-left: 2rem;
    font-size: 2rem;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    max-width: 20rem;
    font-size: 1.4rem;
  }

  p {
    margin: 0;
  }
}

.prizes__list {
  @include list-reset;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  margin-top: auto;

  @media (max-width: $tablet) and (orientation: portrait) {
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    margin: auto 0;
    padding-left: 2rem;
  }
}

.prizes__item {
  margin-left: 7.5rem;
  opacity: 0;
  transition-property: opacity;
  transition-timing-function: ease-out;
  transition-duration: 0.4s;
  transition-delay: 0s;

  @media (min-width: $stop-scaling) {
    margin-left: 75px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    display: flex;
    align-items: flex-end;
    margin-left: 0;
    margin-bottom: 2.8rem;

    &:last-child {
      margin-bottom: 0;
    }
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    margin-left: 5rem;
  }

  .screen--prizes.active & {
    opacity: 1;
  }

  &--journeys {
    transform: translateX(calc(100% + 7rem));
    transition: opacity 0.4s ease-out 0.5s, transform 0.75s ease-out 4s;

    .screen--prizes.active & {
      transform: translateX(0);

      &.animated {
        transition-delay: 0.5s;
      }
    }

    @media (max-width: $tablet) and (orientation: portrait) {
      transform: translateX(0);

      .screen--prizes.active & {
        transition-delay: 0.5s;
      }
    }

    .prizes__icon {
      left: -8.8rem;
      bottom: -0.5rem;
      width: 26.8rem;

      @media (min-width: $stop-scaling) {
        left: -88px;
        bottom: -5px;
        width: 268px;
      }

      @media (max-width: $tablet) and (orientation: portrait) {
        left: 0;
        bottom: 0;
        width: 9.4rem;
      }

      @media (max-width: $tablet) and (orientation: landscape) {
        left: -3.8rem;
        width: 13rem;
      }

      @media (max-width: $mobile) and (orientation: landscape) {
        bottom: -1.2rem;
        left: -3.2rem;
        width: 10rem;
      }
    }
  }

  &--cases {
    margin-left: 7rem;

    .screen--prizes.active & {
      transition-delay: 4s;

      &.animated {
        transition-delay: 1s;
      }
    }

    @media (max-width: $tablet) and (orientation: portrait) {
      .screen--prizes.active & {
        transition-delay: 1s;
      }
    }

    @media (min-width: $stop-scaling) {
      margin-left: 70px;
    }

    @media (max-width: $tablet) and (orientation: portrait) {
      margin-left: 0;
      padding-left: 1.5rem;
    }

    .prizes__icon {
      left: -14.8rem;
      bottom: -0.4rem;
      width: 18.8rem;

      @media (min-width: $stop-scaling) {
        left: -148px;
        bottom: -4px;
        width: 188px;
      }

      @media (max-width: $tablet) and (orientation: portrait) {
        left: 0;
        bottom: 0;
        margin-right: 1.3rem;
        width: 10.211rem;
      }

      @media (max-width: $tablet) and (orientation: landscape) {
        left: -7rem;
        bottom: -0.7rem;
        width: 9.5rem;
      }

      @media (max-width: $mobile) and (orientation: landscape) {
        bottom: -1.2rem;
        left: -5.2rem;
        width: 7rem;
      }
    }
  }

  &--codes {
    .screen--prizes.active & {
      transition-delay: 6.5s;

      &.animated {
        transition-delay: 1.5s;
      }
    }

    @media (max-width: $tablet) and (orientation: portrait) {
      .screen--prizes.active & {
        transition-delay: 1.5s;
      }
    }

    @media (max-width: $tablet) and (orientation: portrait) {
      padding-left: 0.8rem;
    }

    .prizes__icon {
      width: 26rem;
      bottom: -0.4rem;
      left: -4.4rem;

      @media (min-width: $stop-scaling) {
        bottom: -4px;
        left: -44px;
        width: 260px;
      }

      @media (max-width: $tablet) and (orientation: portrait) {
        width: 8.66rem;
        left: 0;
        bottom: 0;
      }

      @media (max-width: $tablet) and (orientation: landscape) {
        left: -2.2rem;
        width: 13rem;
      }

      @media (max-width: $mobile) and (orientation: landscape) {
        bottom: -1.2rem;
        left: -1.8rem;
        width: 10rem;
      }
    }
  }
}

.prizes__icon {
  position: relative;

  @media (max-width: $tablet) and (orientation: portrait) {
    margin-right: 3.8rem;
  }

  svg {
    width: 35rem;
    height: 33rem;

    @media (max-width: $tablet) and (orientation: landscape) {
      width: 17.2rem;
      height: auto;
    }

    @media (max-width: $mobile) and (orientation: landscape) {
      width: 13.2rem;
    }

    @media (max-width: $tablet) and (orientation: portrait) {
      display: none;
    }

    @media (prefers-reduced-motion: reduce) {
      display: none;
    }
  }

  img {
    display: none;
    width: 100%;
    height: auto;

    @media (max-width: $tablet) and (orientation: portrait) {
      display: block;
    }

    @media (prefers-reduced-motion: reduce) {
      display: block;
    }
  }

  .screen--prizes:not(.visited) & {
    display: none;
  }
}

.prizes__desc {
  display: flex;
  align-items: flex-end;
  margin: 5.6rem 0 0 0;
  font-size: 1.8rem;
  line-height: 1.1;

  @media (min-width: $stop-scaling) {
    margin-top: 56px;
    font-size: 18px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    flex-direction: column;
    align-items: flex-start;
    margin-top: 0;
    margin-bottom: -0.5rem;
    margin-left: 0;
    font-size: 1.4rem;
    line-height: 1.5rem;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    margin-top: 3rem;
    font-size: 1.1rem;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    font-size: 0.8rem;
  }

  svg {
    flex: none;
    margin-left: 1rem;
    margin-bottom: 0.3rem;
    width: 1rem;
    height: 1rem;

    @media (min-width: $stop-scaling) {
      margin-left: 10px;
      margin-bottom: 3px;
      width: 10px;
      height: 10px;
    }

    @media (max-width: $tablet) and (orientation: portrait) {
      margin-left: 0;
    }

    @media (max-width: $mobile) and (orientation: landscape) {
      width: 0.5rem;
      height: 0.5rem;
    }
  }
}

.prizes__number {
  font-family: $font-alt;
  font-weight: 400;
  font-size: 12rem;
  line-height: 0.55;
  opacity: 0;
  transition: opacity 0.1s ease-out;

  .prizes__desc.animated & {
    opacity: 1;
  }

  .prizes__item--cases & {
    min-width: 6.6rem;

    @media (max-width: $tablet) and (orientation: landscape) {
      min-width: 3.4rem;
    }
  }

  .prizes__item--codes & {
    min-width: 19.5rem;

    @media (max-width: $tablet) and (orientation: landscape) {
      min-width: 10rem;
    }
  }

  @media (min-width: $tablet) {
    text-align: right;
  }

  @media (min-width: $stop-scaling) {
    font-size: 120px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    font-size: 4.8rem;
    line-height: 0.6;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    font-size: 6rem;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    font-size: 3.5rem;
  }
}

.prizes__desc-text {
  display: flex;
  align-items: flex-end;
  margin-left: 1.4rem;
  max-width: 14rem;
  opacity: 0;
  transform: translateX(1.2em);
  transition: opacity 0.3s ease-out 0.1s, transform 0.3s ease-out 0.1s;

  .prizes__desc.animated & {
    opacity: 1;
    transform: translateX(0);
  }

  @media (min-width: $stop-scaling) {
    margin-left: 14px;
    max-width: 140px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    margin-left: 0;
    max-width: 11rem;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    margin-left: 1rem;
    max-width: 13rem;
  }
}
